//         
.make-column-list-general-rules(xs);
.make-column-list-general-rules(sm);
.make-column-list-general-rules(md);
.make-column-list-general-rules(lg);
.make-column-list-general-rules(xl);
// .make-column-list-general-rules(xxl);

//      ( )
.make-columns(@grid-columns, xs);
.col-xs {
  flex: 1 0 auto;
  width: auto;
  padding-left: (@grid-gutter-width / 2);
  padding-right: (@grid-gutter-width / 2);
}
// .make-offsets(@grid-columns, xs);

//      ( )
@media (min-width: @screen-sm) {
  .make-columns(@grid-columns, sm);
  .col-sm {
    flex: 1 0 auto;
    width: auto;
    padding-left: (@grid-gutter-width / 2);
    padding-right: (@grid-gutter-width / 2);
  }
  // .make-offsets(@grid-columns, sm);
}

